.footer-container{
    background: #252e48;
    padding: 4rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
}
.footer-subscription  {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 24px;
    padding: 24px;
    color: white;
}
.footer-subscription>p{
    font-family: 'Tahoma', 'Lucida Grande','Arial Unicode MS', "Arial Black", sans-serif;
}
.footer-subscription-heading{
    margin-bottom: 24px;
    font-size: 24px;

}

.footer-subscription-text{
    margin-bottom: 24px;
    font-size: 20px;

}
.footer-input{
    padding: 8px 20px;
    border-radius: 2px ;
    margin-right: 10px;
    outline: none;
    border: none;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid white;
}
.footer-links{
    width: 100px;
    max-width: 1000px;
    display: flex;
    justify-content: center;
}
.footer-link-wrapper{
    display: flex;
}
.footer-link-items{
    display: flex;
    flex-direction: column;
    margin:16px;
    text-align: center;
    width: 100%;
    box-sizing: border-box;
}
