.footer-email-form>h2{
    margin-bottom: 32px;
}
.footer-input::placeholder{
    color: #b1b1b1;
}

.social-icon-link{
    color: #fff;
    font-size: 24px;

}
.social-media{
    max-width: 1000px;
    width: 100%;

}

.social-media-wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    margin: 40px auto 0 auto;

}
.social-icons{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
}
.social-logo{
    color: white;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-self: center;
    margin-bottom: 16px;

}
.website-rights{
    color:white;
    margin-bottom: 16px;

}
@media screen and (max-width: 820px){
    .footer-links{
        padding-top: 2rem;

    }
    .footer-input{
        width: 100%;
    }
    .btn{
        width:100%;
    }
    .footer-link-wrapper{
        flex-direction: column;
    }
    .social-media-wrap{
        flex-direction: column;
    }
}